import moment from "moment";

let timeInfo=(row)=>{
  if(row.awardTime){
    return  moment(row.awardTime).format('YYYY-MM-DD')
  }else {
    return ''
  }
};
let timeInfo1=(row)=>{
  if(row.startTime){
    return  moment(row.startTime).format('YYYY-MM-DD')
  }else {
    return ''
  }
};
let timeInfo2=(row)=>{
  if(row.endTime){
    return  moment(row.endTime).format('YYYY-MM-DD')
  }else {
    return ''
  }
};
